import { VOWELS } from 'constant/ipa';
import React from 'react';
import IPAGroupCollapse from './GroupCollapse';

function Vowels() {
  return (
    <>
      <h2 className="dyno-title">Li m u: </h2>
      <h3 className="dyno-sub-title" >
      Bng phin m ting Anh quc t <strong>- International Phonetic Alphabet</strong> (IPA) l bng k hiu ng m quc t m chng ta cn nm vng khi bt u hc ting Anh.
      <br/>
      Gm c <strong>44</strong> m (sounds) trong  c <strong>20</strong> nguyn m (vowel sounds) v <strong>24</strong> ph m (consonant sounds).
      </h3>
      <h3 className="dyno-sub-title">Mnh s chia thnh tng nhm nh  gip cc bn hc vui hn v ghi nh tt hn .</h3>
      <h2 className="dyno-title">1. Nguyn m n (Vowels)</h2>
      <h3 className="dyno-sub-title">
        Chng ta c 12 nguyn m n, mnh chia lm 5 nhm:
      </h3>

      {VOWELS.map((item, key) => (
        <IPAGroupCollapse
          title={item.title}
          phoneticList={item.list}
          key={key}
        />
      ))}
    </>
  );
}

export default Vowels;
