import { CONSONANTS } from 'constant/ipa';
import React from 'react';
import IPAGroupCollapse from './GroupCollapse';

function Consonants() {
  return (
    <>
      <h2 className="dyno-title">3. Ph m (Consonants)</h2>
      <h3 className="dyno-sub-title">
        Chng ta c 24 ph m, mnh chia lm 8 nhm. Lu  v cc dng ph m
        nh m v thanh th khng pht ra m thanh, lung hi khng lm rung dy
        thanh qun. m hu thanh pht ra m thanh, hi th i t hng, qua li,
        rng sau  ra ngoi l cch pht m ca cc m ny, dy thanh qun b
        rung.
      </h3>

      {CONSONANTS.map((item, key) => (
        <IPAGroupCollapse
          title={item.title}
          phoneticList={item.list}
          key={key}
          isNoVoice={true}
        />
      ))}
    </>
  );
}

export default Consonants;
