import { DIPHTHONGS } from 'constant/ipa';
import React from 'react';
import IPAGroupCollapse from './GroupCollapse';

function Diphthongs() {
  return (
    <>
      <h2 className="dyno-title">2. Nguyn m i (Diphthongs)</h2>
      <h3 className="dyno-sub-title">
        Chng ta c 8 nguyn m i, mnh chia lm 3 nhm, pht m theo nguyn
        tc 7/3, 7 phn cho m u v 3 phn m sau:
      </h3>

      {DIPHTHONGS.map((item, key) => (
        <IPAGroupCollapse
          title={item.title}
          phoneticList={item.list}
          key={key}
        />
      ))}
    </>
  );
}

export default Diphthongs;
